import '../styles/secondbar.css'
import { Progress, Space } from 'antd';
import React, { useState } from 'react';

function SecondBar(props){
    return(
        <div class="sidebar">
            <div class="greeting">
                <p class="greeting-text">Hi, User!</p>
                <img src="Profile Picture.png" alt="Profile Picture" class="profile-pic"/>
            </div>

            <div class='all'>
                <p>All tasks</p>
                
                <Progress strokeLinecap="butt" percent={props.progress} />
        
            </div>
            
            <div class="tasks">
                <p>No tasks today</p>
            </div>
            <div class="delete-tasks">
                <p>Delete all tasks</p>
            </div>
        </div>
    )
}

export default SecondBar