import { BellOutlined, SearchOutlined  } from '@ant-design/icons';

import '../styles/navbar.css'

function NavBar(props){
    return(
        <div class="navbar">

            <div class="search">
                <input class="search-input" type ="search" name="" placeholder="Search task" />
                <div class="src"><SearchOutlined style={{fontSize:'20px', color:'blueviolet', margin:'30px 10px'}}/></div>
            </div>

            <button class="btnn"><a href="#">{props.date}</a></button>

            <div class="bell"> <BellOutlined style={{fontSize:'20px', color:'blueviolet', margin:'30px 50px'}} /></div>

            <button class="btn" onClick={props.showModal}>Add new task</button>
        </div>
        
    )
}

    
export default NavBar