import { AppstoreOutlined, MailOutlined, SettingOutlined } from '@ant-design/icons';
import { Menu } from 'antd';


import "../styles/menu.css"

function getItem(label, key, icon, children, type) {
  return {
    key,
    icon,
    children,
    label,
    type,
  };
}

const items = [
    getItem("Today's task", 'grp', null, [
        getItem('All Tasks', '1'),
        getItem('Important Tasks', '2'),
        getItem('Tasks done', '3'),
        getItem('Uncompleted Tasks', '4')],
        'group'),
  ];

function MenuSlider(props){
    
    const change_page=id=>{
        switch (id) {
            case 2:
                props.getTasks("is_favourite")
                break;
            case 3:
                props.getTasks("is_completed")
                break;
            case 4:
                props.getTasks("is_completed",false)
                break;
            default:
                props.getTasks()
                break;
        }
    }
    return(
        <div id='mainbar'>
            <div id="main-heading">TO-DO TASK</div>
            
            <div id="add-task" onClick={props.showModal}>Add new task</div>

            <div id="menu">
            <Menu
                onClick={(e)=>{
                    change_page(parseInt(e.key))
                }}
                style={{
                    width: 256,
                }}
                defaultSelectedKeys={['1']}
                defaultOpenKeys={['sub1']}
                mode="inline"
                items={items}
                />
            </div>

        </div>
    )
}

export default MenuSlider