import '../styles/task.css'
import { StarOutlined,StarFilled, DeleteFilled, MoreOutlined, CalendarOutlined, FieldTimeOutlined  } from '@ant-design/icons';

function Task(props){
    return(
        <div class="form">
            <h2>{props.data.name}</h2>
            <p>{props.data.description}</p>

            <div class="can">
                <CalendarOutlined style={{fontSize:'20px', color:'white', marginTop:'5px' }}/> 
                <h2>{props.data.date}</h2>
            </div>

            <div class="time">
                <FieldTimeOutlined style={{fontSize:'20px', color:'white', marginTop:'5px' }}/> 
                <h2>{props.data.time}</h2>
            </div>
            
            <div class="last">
                <button class="com" onClick={()=>{props.toggleComplete(props.data.id)}}>{props.data.is_completed?'completed':'mark as done'}</button>
                {
                !props.data.is_favourite?
                    <StarOutlined 
                        style={{fontSize:'20px', color:'white', margin:'0px 10px'}} 
                        onClick={()=>{props.toggleFavourite(props.data.id)}} 
                    />
                :
                    <StarFilled 
                        style={{fontSize:'20px', color:'white', margin:'0px 10px'}} 
                        onClick={()=>{props.toggleFavourite(props.data.id)}} 
                    />
                }
                <DeleteFilled onClick={()=>{props.removeTask(props.data.id)}} style={{fontSize:'20px', color:'white', margin:'0px 10px'}}/>
                
            </div>
        </div>
        
    )
}

export default Task