import logo from './logo.svg';
import './App.css';

import MenuSlider from './components/menu';
import NavBar from './components/navbar';
import Task from './components/task';
import SecondBar from './components/secondbar';

import { Modal } from 'antd';
import { useState, useEffect } from 'react';

import { task_data } from './data/tasks';

function App() {

  const [all_tasks,setTask] = useState([])
  const [all_tasks_copy,setTaskCopy] = useState([])
  const [date,setDate] = useState("null")
  const [progress, setProgress] = useState(0)


  useEffect(()=>{
    setTask(task_data)
    setTaskCopy(task_data)

    const date = new Date();

    let day = date.getDate();
    let month = date.toLocaleString('default', { month: 'short' });
    let year = date.getFullYear();

    // This arrangement can be altered based on how we want the date's format to appear.
    let currentDate = `${day}, ${month} ${year}`;
    setDate(currentDate)
    update_progress()


  },[])

  const update_progress=(data=null)=>{
    const my_tasks = data?data:task_data

    const completed = my_tasks.filter(e=>{return e.is_completed})
    const progress_per_task = 100 / my_tasks.length
    setProgress(parseInt(completed.length * progress_per_task))
  }


  const task_template = {
    name:'',
    description:"",
    date:"",
  }

  const [isModalOpen, setIsModalOpen] = useState(false); 

  const [taskForm,setTaskForm] = useState(task_template)

  const updateTaskForm=(field,value)=>{
    const form = {...taskForm}
    form[field] = value
    setTaskForm(form)
  }

  const showModal = () => {
    setIsModalOpen(true);
  };
  const handleOk = () => {

    if(check_form()){
      addTask()
      setIsModalOpen(false);
    }
    
  };
  const handleCancel = () => {
    setIsModalOpen(false);
  };

  const check_form=()=>{
    for(let i in taskForm){
      if(taskForm[i].length === 0){
        return false
      }
    }
    return true
  }

  const addTask=()=>{
    const form = {...taskForm,
      is_completed:false,
      is_favourite:false,
      id:all_tasks.length + 1
    }
    const data = [...all_tasks]
    data.push(form)

    setTask(data)
    setTaskCopy(data)
    setTaskForm(task_template)
    update_progress(data)
  }

  const getTasks=(option=null,value=true)=>{

    if(option == null){
      setTask(all_tasks_copy)
    }else{
      console.log(option," ", value);
      let data = all_tasks_copy
      data = data.filter(e=>{
        return e[option] === value
      })

      setTask(data)
    }
  }

  const removeTask=id=>{
    let data = [...all_tasks]
    data = data.filter(e=>{return e.id !== id})
    setTask(data)
    setTaskCopy(data)
    update_progress(data)
  }

  const toggleFavourite=id=>{
    let data = [...all_tasks]
    const current = data.find(e=>{return e.id === id})
    const index = all_tasks.indexOf(current)
    current.is_favourite = !current.is_favourite

    data[index] = current

    setTask(data)
  }

  const toggleComplete=id=>{
    let data = [...all_tasks]
    const current = data.find(e=>{return e.id === id})
    const index = all_tasks.indexOf(current)
    current.is_completed = !current.is_completed

    data[index] = current

    setTask(data)
    update_progress(data)
  }


  return (
    <>
    <div id="container">
      
      <MenuSlider showModal={showModal} getTasks={getTasks}/>

      <div id="content">
        <NavBar showModal={showModal} date={date}/>

        <div id="task-container">
          {all_tasks.map((e,index)=>(
            <Task 
              data={e} 
              key={index}
              removeTask={removeTask}
              toggleFavourite={toggleFavourite}
              toggleComplete={toggleComplete}
            />
          ))}

        </div>
        
      </div>

      <SecondBar progress={progress}/>
    </div>
    <Modal title="Add Task" open={isModalOpen} onOk={handleOk} onCancel={handleCancel}>

      <form id='taskForm'>
            <div for="taskName">Task Name:</div>
            <input type="text" id="taskName" name="taskName" required value={taskForm['name']} onInput={(e)=>{
              updateTaskForm('name',e.target.value)
            }}></input><br></br>

            <div for="description">Description:</div>
            <textarea id="description" name="description" rows="4" cols="50" value={taskForm['description']}  onInput={(e)=>{
              updateTaskForm('description',e.target.value)
            }}></textarea><br></br>

            <div for="dueDate">Due Date:</div>
            <input type="date" id="dueDate" name="dueDate" value={taskForm['date']} onInput={(e)=>{
              updateTaskForm('date',e.target.value)
            }}/><br></br>

            <div for="dueTime">Due Time:</div>
            <input type="time" id="dueTime" name="dueTime" value={taskForm['time']} onInput={(e)=>{
              updateTaskForm('time',e.target.value)
            }}/><br></br>


          </form>
      {/* <h1>Some contents...</h1>
      <p>Some contents...</p>
      <p>Some contents...</p> */}
    </Modal>
    </>
  );
}

export default App;
